.small-el-form.el-form .el-form-item {
  margin-bottom: 9px;
  .el-form-item__label,
  .el-form-item__content {
    line-height: 24px;
  }

  &.flex-grid {
    display: flex;
    align-items: center;
    .el-form-item__content {
      margin-left: 0 !important;
    }
  }
}



